  vertical-align: top;
  user-select: none;
  pointer-events: none;
  width: 100%;
  height: auto;
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
