.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preloader__loader {
  width: 48px;
  height: 48px;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.preloader__loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #ff3d00 transparent;
}

.preloader-remove {
  display: none;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.parallax {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100vh;
  min-height: 100%;
  pointer-events: none;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
.parallax__image {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgb(2, 1, 1);
}
.parallax__image img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.2;
  object-fit: cover;
  object-position: top center;
  will-change: transform;
}
.parallax__image-achieve {
  filter: grayscale(91%);
}
.parallax__image-services {
  object-position: bottom center;
}
.parallax__services-company {
  filter: grayscale(1);
  opacity: 0.1 !important;
}
.parallax picture {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  will-change: transform;
}

.burger-button {
